
import {
  defineComponent,
  computed,
  onMounted,
  ref,
  watch,
  reactive,
} from "vue";
import { useRoute, useRouter } from "vue-router";
import { useStore } from "@/store";
import { helper as $h } from "@/utils/helper";
import TopBar from "@/components/top-bar/Main.vue";
import MobileMenu from "@/components/mobile-menu/Main.vue";
import DarkModeSwitcher from "@/components/dark-mode-switcher/Main.vue";
import SideMenuTooltip from "@/components/side-menu-tooltip/Main.vue";
import { linkTo, nestedMenu, enter, leave } from "./index";
import DarkModeSwitcherMenu from "@/components/dark-mode-switcher-menu/Main.vue";
import { api } from "@/utils/api-config";
import { VueCookieNext as $ck } from "vue-cookie-next";

interface ListMenu {
  icon: string;
  pageName: string;
  title: string;
}

export default defineComponent({
  components: {
    TopBar,
    MobileMenu,
    DarkModeSwitcher,
    SideMenuTooltip,
    DarkModeSwitcherMenu,
  },
  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const formattedMenu = ref([] as string[] | ListMenu[]);
    const role: string = api.getRole();

    const menuList = reactive({
      listMenu: $ck.getCookie("list-menu"),
    });

    const getListMenu = (): any => {
      const listMenu = JSON.parse(menuList.listMenu);
      return listMenu;
      // if ( role === "member" ) {
      //   return store.state.sideMenu.member;
      // } else if ( role === "admin" ) {
      //   return store.state.sideMenu.admin;
      // } else if ( role === "affiliate" ) {
      //   return store.state.sideMenu.affiliate;
      // } else if ( role === "sales" ) {
      //   return store.state.sideMenu.sales;
      // } else if ( role === "keuangan" ) {
      //   return store.state.sideMenu.keuangan;
      // } else if ( role === "tentor" ) {
      //   return store.state.sideMenu.tentor;
      // } else if ( role === "owner" ) {
      //   return store.state.sideMenu.owner;
      // } else {
      //   return store.state.sideMenu.member;
      // }
    };

    // const listMenu = getListMenu();
    const sideMenu = computed(() => nestedMenu(getListMenu(), route, role));

    watch(
      computed(() => route.path),
      () => {
        formattedMenu.value = $h.toRaw(sideMenu.value);
      }
    );

    onMounted(() => {
      cash("body")
        .removeClass("error-page")
        .removeClass("login")
        .addClass("app");
      formattedMenu.value = $h.toRaw(sideMenu.value);
    });

    return {
      formattedMenu,
      router,
      linkTo,
      enter,
      leave,
      role,
    };
  },
});
